.CreateModal {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: none;
    background: rgba(0, 0, 0, 0.3);
}

.CreateModalContent {
    padding: 25px;
    background: rgb(240, 236, 229);
    border-radius: 16px;
    min-width: 450px;
}


.CreateModal.active {
    display: flex;
    justify-content: center;
    align-items: center;
}